.chat {
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  // align-items: center;
  height: 100vh;

  .top {
    // position: fixed;
    display: flex;
    align-items: center;
    width: 100%;
    height: 70px;
    background-color: rgb(35, 35, 35);

    .back {
      padding: 15px 5px 15px 15px;
      user-select: none;
    }

    .avatar {
      width: 50px;
      height: 50px;
      margin: 0 calc((70px - 50px) / 2);
      background-size: cover;
      background-position: center;
      border-radius: 200px;
    }

    .name {
      font-size: 20px;
      text-decoration: none;
    }
  }

  .messages {
    display: flex;
    flex-flow: column;
    width: 100%;
    margin: 70px 0 54px + 36px + 15px 0;
    padding: 0 calc((100vw - 85%) / 2);
    padding-top: 15px;
    padding-bottom: 15px - 10px;

    .message {
      width: fit-content;
      margin-bottom: 10px;
      padding: 10px;
      background-color: rgb(35, 35, 35);
      border-radius: 20px;
    }
  }

  .send {
    // position: fixed;
    // bottom: 54px;
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 100%;
    padding: 15px;

    textarea {
      width: 100%;
      max-height: 300px;
      padding: 6px 50px 6px 6px;
      border: none;
      border-radius: 8px;
      background-color: rgb(35, 35, 35);
      outline: none;
      font-size: 20px;
      resize: none;
      z-index: 1;

      margin-bottom: 55px;
    }

    .icon {
      // position: absolute;
      bottom: calc(15px + 36px / 2);
      right: calc(15px);
      transform: translateY(50%);
      z-index: 1;
      padding: 6px;
      border-radius: 8px;
      cursor: pointer;
      user-select: none;
    }

    .messages-hidder {
      position: absolute;
      bottom: 0;
      transform: translateY(2px); // for overlapping with menu
      width: calc(100% - 30px);
      height: calc(15px + 36px / 2 + 2px);
      background-color: #181818;
    }
  }
}
